import React, { Component } from "react";
import { MdSearch } from "react-icons/md";
import { claimReasonInterface } from "../../../interfaces";
import search from "../../../utils/search";

export const claimReasonsData = [
  { key: 1, reason: "Not placed (Sinahawe umwanya)" },
  {
    key: 2,
    reason:
      "Not placed with higher marks (Sinahawe umwanya kandi hari abo ndusha twakoze bimwe bawuhawe)",
  },
  {
    key: 3,
    reason:
      "Want to change district of placement (Nifuza guhindurirwa akarere nahawemo umwanya)",
  },
  {
    key: 4,
    reason: "Rejected offer by mistake (Nibeshye nanga umwanya nahawe)",
  },
  {
    key: 0,
    reason: "None of the above (nta na kimwe muri ibyo byavuzwe haruguru)",
  },
];

type ChooseReasonProps = {
  claimReason: claimReasonInterface | null;
  setClaimReason: (value: claimReasonInterface | null) => void;
  setChooseClaimReason: (value: boolean) => void;
  allClaimReasons: claimReasonInterface[];
};

type ChooseReasonState = {
  searchInput: string;
};

class ChooseReason extends Component<ChooseReasonProps, ChooseReasonState> {
  constructor(props: ChooseReasonProps) {
    super(props);

    this.state = {
      searchInput: "",
    };
  }

  setSearchInput = (value: string) => {
    this.setState({ searchInput: value });
  };

  render() {
    return (
      <div>
        {/* SEARCH PART */}
        <div className="flex items-center w-full my-1">
          <MdSearch className="w-6 h-6 absolute ml-2" />
          <input
            type="text"
            placeholder="Search"
            className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
            onChange={(e) => this.setSearchInput(e.target.value)}
            value={this.state.searchInput}
          />
          <button
            className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
            type="button"
            onClick={() => this.props.setChooseClaimReason(false)}
          >
            Cancel
          </button>
        </div>
        {/* END SEARCH PART ///////////////// */}

        <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
          {search(this.props.allClaimReasons, this.state.searchInput).map(
            (item: claimReasonInterface, index) => (
              <section key={index.toString() + item.id}>
                <div
                  className={`my-1 py-1 w-full px-2 hover:bg-gray-100 cursor-pointer ${
                    this.props.claimReason &&
                    this.props.claimReason.id === item.id &&
                    "border-l-2 border-blue-600"
                  }`}
                  onClick={() => {
                    this.props.setChooseClaimReason(false);
                    this.props.setClaimReason(item);
                  }}
                >
                  <p className="text-base text-black">{item.reason}</p>
                </div>
              </section>
            )
          )}
        </div>
      </div>
    );
  }
}

export default ChooseReason;
