import React, { useState } from "react";
import { MdSearch } from "react-icons/md";
import { connect } from "react-redux";
import {
  course,
  CandidateClaimInterface,
  claimApplicationsInterface,
  ClaimExamResultsDetails,
  SessionPlanInterface,
} from "../../../interfaces";
import { StoreState } from "../../../reducers";
import { search } from "../../../utils/functions";
import { isClaimAllowed } from "./ClaimForm";

const getFailedExam = (
  allData: ClaimExamResultsDetails[],
  allCourses: course[]
): ClaimExamResultsDetails[] => {
  let result: ClaimExamResultsDetails[] = [];
  allData.forEach((element) => {
    if (element.total_marks <= 50) {
      let data = element;
      data.course_name = getCourseById(allCourses, element.course)
        ? getCourseById(allCourses, element.course)!.course_name
        : "Unknown exam";
      result.push(data);
    }
  });

  return result;
};

export const getCourseById = (courses: course[], course_id: string) => {
  return courses.find((item) => item.course_id === course_id);
};

export const getCourseIdBySheetId = (
  allData: ClaimExamResultsDetails[],
  sheet_id: string
) => {
  return allData.find((item) => item.answer_sheet_id === sheet_id);
};

export const searchApplication = (
  objList: claimApplicationsInterface[],
  text: any
): claimApplicationsInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (prop === "position") {
        flag =
          product.position.position_title
            .toString()
            .toLowerCase()
            .indexOf(text.toLowerCase()) > -1;
      }
      if (flag) break;
    }
    return flag;
  });
  // return objList.filter((product) =>
  //   product.position.position_title.includes(text)
  // );
};

type FormExamListProps = {
  setChooseClaimFile: (value: boolean) => void;
  setClaimFileId: (value: string) => void;
  setViewThisFile: (value: string) => void;
  viewThisFile: string;
  candidateAnswers: ClaimExamResultsDetails[];
  myClaims: CandidateClaimInterface[];
  activeSession: SessionPlanInterface;

  // REDUX //////////////////////
  courses: course[] | null;
};

const FormExamList: React.FC<FormExamListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");

  return (
    <section>
      {/* SEARCH PART */}
      <div className="flex items-center w-full my-1">
        <MdSearch className="w-6 h-6 absolute ml-2" />
        <input
          type="text"
          placeholder="Search"
          className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
          onChange={(e) => setSearchInput(e.target.value)}
          value={searchInput}
          disabled={
            !props.courses ||
            getFailedExam(props.candidateAnswers, props.courses).filter(
              (item) =>
                isClaimAllowed(
                  props.myClaims,
                  props.activeSession.session_plan_id,
                  item.answer_sheet_id
                )
            ).length > 0
              ? false
              : true
          }
        />
        <button
          className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
          type="button"
          onClick={() => {
            props.setChooseClaimFile(false);
            props.setViewThisFile("");
          }}
        >
          Cancel
        </button>
      </div>
      {/* END SEARCH PART ///////////////// */}{" "}
      {/* searchData(props.candidateAnswers, searchInput) */}
      <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
        {props.courses &&
        getFailedExam(props.candidateAnswers, props.courses).filter((item) =>
          isClaimAllowed(
            props.myClaims,
            props.activeSession.session_plan_id,
            item.answer_sheet_id
          )
        ).length > 0 ? (
          search(
            getFailedExam(props.candidateAnswers, props.courses).filter(
              (item) =>
                isClaimAllowed(
                  props.myClaims,
                  props.activeSession.session_plan_id,
                  item.answer_sheet_id
                )
            ),
            searchInput
          ).map((item: ClaimExamResultsDetails, index: number) => (
            <div
              className="group flex items-center justify-between my-1 hover:bg-gray-100"
              key={index + item.answer_sheet_id}
            >
              <div
                className="p-1 w-full mr-2 cursor-pointer"
                onClick={() => {
                  props.setChooseClaimFile(false);
                  props.setClaimFileId(item.answer_sheet_id);
                }}
              >
                <p className="text-base font-medium text-black">
                  {item.course_name}
                </p>
              </div>
              <button
                type="button"
                className="bg-blue-200 hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                onClick={() => {
                  props.setViewThisFile(item.answer_sheet_id);
                }}
              >
                View
              </button>
            </div>
          ))
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No failed exam available
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

const mapStateToProps = ({
  course,
}: StoreState): {
  courses: course[] | null;
} => {
  return {
    courses: course.courses,
  };
};
export default connect(mapStateToProps, {})(FormExamList);
