import React, { useState } from "react";
import { MdSearch } from "react-icons/md";
import { connect } from "react-redux";
import {
  course,
  CandidateClaimInterface,
  claimApplicationsInterface,
  ClaimExamResultsDetails,
  SessionPlanInterface,
} from "../../../interfaces";
import { candidatePlacementInterface } from "../../../interfaces/placement";
import { isClaimAllowed } from "./ClaimForm";

export const getCourseById = (courses: course[], course_id: string) => {
  return courses.find((item) => item.course_id === course_id);
};

export const getCourseIdBySheetId = (
  allData: ClaimExamResultsDetails[],
  sheet_id: string
) => {
  return allData.find((item) => item.answer_sheet_id === sheet_id);
};

export const searchApplication = (
  objList: claimApplicationsInterface[],
  text: any
): claimApplicationsInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (prop === "position") {
        flag =
          product.position.position_title
            .toString()
            .toLowerCase()
            .indexOf(text.toLowerCase()) > -1;
      }
      if (flag) break;
    }
    return flag;
  });
  // return objList.filter((product) =>
  //   product.position.position_title.includes(text)
  // );
};

type FormPlacementListProps = {
  setChooseClaimFile: (value: boolean) => void;
  setClaimFileId: (value: string) => void;
  setViewThisFile: (value: string) => void;
  viewThisFile: string;
  myPlacement: candidatePlacementInterface | null;
  myClaims: CandidateClaimInterface[];
  activeSession: SessionPlanInterface;

  // REDUX //////////////////////
};

const FormPlacementList: React.FC<FormPlacementListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");

  return (
    <section>
      {/* SEARCH PART */}
      <div className="flex items-center w-full my-1">
        <MdSearch className="w-6 h-6 absolute ml-2" />
        <input
          type="text"
          placeholder="Search"
          className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
          onChange={(e) => setSearchInput(e.target.value)}
          value={searchInput}
          disabled={
            props.myPlacement === null ||
            isClaimAllowed(
              props.myClaims,
              props.activeSession.session_plan_id,
              props.myPlacement.placed._id
            )
              ? false
              : true
          }
        />
        <button
          className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
          type="button"
          onClick={() => {
            props.setChooseClaimFile(false);
            props.setViewThisFile("");
          }}
        >
          Cancel
        </button>
      </div>
      {/* END SEARCH PART ///////////////// */}{" "}
      {/* searchData(props.candidateAnswers, searchInput) */}
      <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
        {props.myPlacement ? (
          isClaimAllowed(
            props.myClaims,
            props.activeSession.session_plan_id,
            props.myPlacement.placed._id
          ) && (
            <div className="group flex items-center justify-between my-1 hover:bg-gray-100">
              <div
                className="p-1 w-full mr-2 cursor-pointer"
                onClick={() => {
                  props.setChooseClaimFile(false);
                  props.setClaimFileId(props.myPlacement!.placed._id);
                }}
              >
                <p className="text-base font-medium text-black">
                  Placed on {props.myPlacement.placed.school.school_name}
                </p>
              </div>
              <button
                type="button"
                className="bg-blue-200 hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                onClick={() => {
                  props.setViewThisFile(props.myPlacement!.placed._id);
                }}
              >
                View
              </button>
            </div>
          )
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No placement to claim on!
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

