import axios from "axios";
import React, { Component } from "react";
import { VscLoading } from "react-icons/vsc";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import { updateSessionPlan } from "../../actions/session";
import { PopupAlertData } from "../../components/Alert/Popup";
import {
  allPlanTypes,
  PlansInterface,
  PlansTypeInterface,
  PlansTypeKeys,
  SessionPlanInterface,
  SessionPlanTypeStatus,
} from "../../interfaces";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { capitalizeFirstLetter } from "../../shared/dataTest";
import { API } from "../../utils/api";
import { DATE, errorToText } from "../../utils/functions";

export const isActivationAllowed = (thisPlans: PlansInterface): boolean => {
  let today: Date = new Date(),
    end_date = new Date(thisPlans.end_date);
  if (end_date.getTime() >= today.getTime()) {
    return true;
  }
  return false;
};

export const getPlanTypeByKey = (
  type_key: PlansTypeKeys
): PlansTypeInterface | undefined => {
  return allPlanTypes.find((item) => item.type_key === type_key);
};

export const isPlanActive = (
  viewThisSession: SessionPlanInterface,
  thisPlan: PlansInterface
): "OPEN" | "CLOSED" | "WAITING" => {
  if (viewThisSession.is_active) {
    let today = new Date(),
      planEndDate = new Date(thisPlan.end_date),
      planStartDate = new Date(thisPlan.start_date);
    if (
      planEndDate.getTime() >= today.getTime() &&
      planStartDate.getTime() <= today.getTime() &&
      thisPlan.type_status !== SessionPlanTypeStatus.CLOSED
    ) {
      return "OPEN";
    } else if (
      planStartDate.getTime() > today.getTime() &&
      thisPlan.type_status !== SessionPlanTypeStatus.CLOSED
    ) {
      return "WAITING";
    }
  }
  return "CLOSED";
};

type SessionDetailsProps = {
  viewThisSession: SessionPlanInterface;
  setViewThisSession: (data: SessionPlanInterface) => void;

  // REDUX ----------------
  updateSessionPlan: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

type SessionDetailsState = {
  loadingRequest: string;
};

export class SessionDetails extends Component<
  SessionDetailsProps,
  SessionDetailsState
> {
  constructor(props: SessionDetailsProps) {
    super(props);

    this.state = {
      loadingRequest: "",
    };
  }

  handlePlanActivation = async (
    session: SessionPlanInterface,
    thisPlan: PlansInterface
  ) => {
    const newState: "open" | "close" = session.is_active ? "close" : "open";

    if (!window.confirm(`Are you sure you want to ${newState} this plan?`)) {
      return true;
    }

    if (!isActivationAllowed(thisPlan)) {
      this.props.PushPopupAlert({
        title: `Opening/Closing plan which has been expired is not allowed!`,
        theme: PopupAlertTheme.warning,
      });
    }

    let newData: SessionPlanInterface = {
      session_plan_name: session.session_plan_name,
      period: session.period,
      is_active: session.is_active,
      plans: [],
      createdAt: session.createdAt,
      updatedAt: session.updatedAt,
      session_plan_id: session.session_plan_id,
    };
    for (const key in session.plans) {
      if (Object.prototype.hasOwnProperty.call(session.plans, key)) {
        const element = session.plans[key];
        if (thisPlan.type_key === element.type_key) {
          if (thisPlan.type_status === SessionPlanTypeStatus.OPEN) {
            newData.plans.push({
              type_id: thisPlan.type_id,
              type_title: thisPlan.type_title,
              type_key: thisPlan.type_key,
              type_status: SessionPlanTypeStatus.CLOSED,
              start_date: thisPlan.start_date,
              end_date: thisPlan.end_date,
              allowed_claim_level: thisPlan.allowed_claim_level,
              _id: thisPlan._id,
              history: thisPlan.history,
            });
          } else {
            newData.plans.push({
              type_id: thisPlan.type_id,
              type_title: thisPlan.type_title,
              type_key: thisPlan.type_key,
              type_status: SessionPlanTypeStatus.OPEN,
              start_date: thisPlan.start_date,
              end_date: thisPlan.end_date,
              allowed_claim_level: thisPlan.allowed_claim_level,
              _id: thisPlan._id,
              history: thisPlan.history,
            });
          }
        } else {
          newData.plans.push(element);
        }
      }
    }

    console.log("====================================");
    console.log({ newData });
    console.log("====================================");

    this.setState({ loadingRequest: thisPlan.type_key });
    try {
      const res = await axios.put<SessionPlanInterface>(
        `${API.CLAIMING}/session/${session.session_plan_id}`,
        newData
      );
      this.props.updateSessionPlan(res.data);
      this.props.setViewThisSession(res.data);
      this.props.PushPopupAlert({
        title: `Session ${newState}d successfully`,
        theme: PopupAlertTheme.info,
      });
      this.setState({ loadingRequest: "" });
      console.log("updateSessionPlan: ", res);
    } catch (error) {
      console.error({ error: errorToText(error) });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.warning,
      });
    }
  };

  render() {
    return (
      <table className="w-full bg-white shadow p-5" id="session_content">
        <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
          <tr>
            <td className="p-2">#</td>
            <td className="p-2">Plan Type</td>
            <td className="p-2">Start Date</td>
            <td className="p-2">End date</td>
            {this.props.viewThisSession.is_active && (
              <td className="p-2" colSpan={2}>
                Status
              </td>
            )}
          </tr>
        </thead>
        <tbody>
          {/* WHILE DATA FOUND ====================== */}
          {this.props.viewThisSession.plans.map((item, index) => (
            <tr
              key={index + item._id}
              className={`border-b border-gray-200 hover:bg-gray-100`}
            >
              <td className="p-2 text-left whitespace-nowrap">{index + 1}</td>
              <td className="p-2 text-left">
                <span className="text-lg">
                  {getPlanTypeByKey(item.type_key) &&
                    capitalizeFirstLetter(
                      getPlanTypeByKey(item.type_key)!.type_title
                    )}
                </span>
              </td>
              <td className="p-2 text-left">{DATE(item.start_date)}</td>
              <td className="p-2 text-left">{DATE(item.end_date)}</td>
              {this.props.viewThisSession.is_active && (
                <td>
                  {isPlanActive(this.props.viewThisSession, item) === "OPEN" ? (
                    <span className={`px-2 py-1 font-semibold text-green-700`}>
                      Open
                    </span>
                  ) : isPlanActive(this.props.viewThisSession, item) ===
                    "WAITING" ? (
                    <span className={`px-2 py-1 font-semibold text-gray-700`}>
                      Waiting
                    </span>
                  ) : (
                    <span className={`px-2 py-1 font-semibold text-red-700`}>
                      Closed
                    </span>
                  )}
                </td>
              )}
              {this.props.viewThisSession.is_active && (
                <td>
                  {isActivationAllowed(item) && (
                    <div className="flex items-center justify-center gap-1">
                      <div
                        className={`relative ${
                          this.state.loadingRequest.length > 0
                            ? "cursor-not-allowed"
                            : "cursor-pointer"
                        }`}
                        title={
                          item.type_status === SessionPlanTypeStatus.OPEN
                            ? "Deactivating session"
                            : "Activating session"
                        }
                        onClick={() =>
                          this.state.loadingRequest.length <= 0 &&
                          this.handlePlanActivation(
                            this.props.viewThisSession,
                            item
                          )
                        }
                      >
                        <span className="block w-14 h-8 bg-gray-100 rounded-full shadow-inner"></span>
                        <span
                          className={`absolute block w-6 h-6 mt-1 ml-1 rounded-full shadow inset-y-0 left-0 focus-within:shadow-outline transition-transform duration-500 ease-in-out ${
                            item.type_status === SessionPlanTypeStatus.CLOSED
                              ? "bg-red-500"
                              : "bg-green-500 transform translate-x-full"
                          }`}
                        ></span>
                      </div>
                      {this.state.loadingRequest === item.type_key && (
                        <VscLoading className="animate__animated animate__rotateIn animate__infinite" />
                      )}
                    </div>
                  )}
                </td>
              )}
            </tr>
          ))}
        </tbody>
      </table>
    );
  }
}

export default connect(null, {
  updateSessionPlan,
  PushPopupAlert,
})(SessionDetails);
