type ExaminationFormViewProps = {
  thisAnswerSheet: ClaimExamResultsDetails;
  setViewThisFile: (value: string) => void;
  setChooseClaimFile: (value: boolean) => void;
  setClaimFileId: (value: string) => void;
};

const ExaminationFormView: React.FC<ExaminationFormViewProps> = (props) => {
  return (
    <section className="">
      <div className="w-full bg-gray-100 px-4 py-1 animate__animated animate__fadeIn">
        <div className="flex flex-col mb-2">
          {/* APPLICATION TITLE ========== */}
          <section className="flex items-center justify-between">
            <span className="text-blue-600 font-bold text-2xl">
              {props.thisAnswerSheet.course}
            </span>
            <button
              type="button"
              className="btn btn-blue"
              onClick={() => {
                props.setChooseClaimFile(false);
                props.setClaimFileId(props.thisAnswerSheet.answer_sheet_id);
                props.setViewThisFile("");
              }}
            >
              Choose
            </button>
          </section>
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Date:</span>
            <span className="font-bold text-gray-900 py-1">
              {DATE(props.thisAnswerSheet.createdAt)}
            </span>
          </div>
        </div>
        <div className="bg-white w-full rounded p-2 mb-2">
          <div className="text-gray-800 text-2xl font-bold my-2">Questions</div>
          {props.thisAnswerSheet.questions.map((item, index) => (
            <div
              key={index + item.id}
              className="rounded border my-1 p-1 divide-y"
            >
              <div className="flex flex-col gap-1 text-base text-gray-600">
                <span className={`font-semibold`}>
                  {item.question.find(
                    (q) => q.language === QuestionLanguage.Eng
                  )
                    ? item.question.find(
                        (q) => q.language === QuestionLanguage.Eng
                      )!.title
                    : item.question[0].title}
                  :
                </span>
                <span className="">
                  {item.question.find(
                    (q) => q.language === QuestionLanguage.Eng
                  )
                    ? item.question.find(
                        (q) => q.language === QuestionLanguage.Eng
                      )!.description
                    : item.question[0].description}
                </span>
                <div className="flex items-center gap-2">
                  <span className="text-medium">Right answer</span>
                  <span className="">
                    {item.question.find(
                      (q) => q.language === QuestionLanguage.Eng
                    )
                      ? item.question.find(
                          (q) => q.language === QuestionLanguage.Eng
                        )!.description
                      : item.question[0].answer}
                  </span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ExaminationFormView;
